.mainCart {
  background: #fff;
  background-position: center;
  background-size: cover;
  padding-top: 1rem;
  padding-bottom: 1rem;
  height: auto;
}
.mainwishlist {
  width: 100%;
  height: auto;
  padding: 10px;
}
@media (max-width: 550px) {
  .Header {
    width: 100%;
    position: relative;
  }
  .hamburger {
    display: block;
    position: absolute;
    top: 20px;
    font-size: 20px;
    z-index: 1000;
  }
  .navbar-support,
