  .navbar-support.show ul {
    display: flex;
    flex-direction: column;
    justify-self: center;
  }
  .navbar-support.show ul li a,
  .navbar-user.show ul li a {
    color: #fff;
    transition: 0.3s ease-in;
    cursor: pointer;
  }
  .navbar-support.show ul li a:hover,
  .navbar-user.show ul li a:hover {
    color: #9a9265;
  }
  .mobile-menu {
    position: absolute;
    display: flex;
    flex-direction: column;
    width: 100%;
    margin-top: 75px;
    background: rgba(0, 0, 0, 0.8);
    z-index: 999;
    backdrop-filter: blur(5px);
  }
  .formheader {
    display: none;
  }
  .WeDo {
    width: 80%;
  }
  .WeDo ul li p {
    font-size: 6px;
    font-family: serif;
    font-weight: lighter;
  }
  .banner {
    height: 40vh;
  }
  .banner-header h2 {
    font-size: 15px;
  }
  .banner-header p {
    font-size: 10px;
  }
  .card-content h2 {
    font-size: 15px;
  }
  .custom-arrow {
    display: none;
  }
  .card-content1 {
    display: flex;
    flex-direction: column;
    width: 90%;
    margin: 0 auto;
  }
  .summer {
    margin-top: 115rem;
    margin-bottom: 1rem;
    height: 120vh;
  }
  .summmer-content {
    display: flex;
    flex-direction: column;
    width: 90%;
    margin: 0 auto;
  }
  .summer-content-header h2 {
    font-size: 12px;
  }
  .secondCard {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    width: 90%;
    margin: 0 auto;
  }
  .about {
    margin-top: 115rem;
  }
  .about-image {
    display: none;
  }
  .about_text {
    width: 100%;
    height: 470px;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .card-content h2 {
    font-size: 13px;
  }
  .accesories {
    display: flex;
    flex-direction: column;
