    grid-template-columns: repeat(3, 2fr);
    height: 50vh;
  }
  .footer-link {
    height: 8;
  }
  .footer-link h3 {
    font-size: 10px;
  }
  .foot p {
    font-size: 10px;
  }
}
@media (max-with: 1400px) {
  .container {
    width: 1400px;
    margin: 0 auto;
